
/* Sidebar 

<div>Icons made by <a href="https://www.flaticon.com/authors/good-ware" title="Good Ware">Good Ware</a> from <a href="https://www.flaticon.com/" title="Flaticon">www.flaticon.com</a></div>
*/
.trigger {
    z-index: 2;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 4em;
 }
 .trigger > i {
    display: inline-block;
    padding: 15px;
    color: black;
 }


 .nav {
   z-index: 1;
    margin: 1em;
    margin-bottom: 2em;
    border-radius: 10px;
    position: fixed;
    top: 0;
    left: -25em;
    overflow: hidden;
    transition: all .3s ease-in;
    width: 15em;
    height: 98%;
    background: #EAEBF1;
    color: #3F7209;
    overflow-y: scroll;
    box-shadow:0 0 8px #a0a19f;
 }
 .trigger:hover {
   cursor: pointer;
 }
 .nav:hover,
 .nav:focus,
 .trigger:focus + .nav,
 .trigger:hover + .nav {
    left: 0;
 }
 
 .nav ul {
    position: absolute;
    top: 4em;
    left: 0;
    margin: 0;
    padding: 0;
    width: 15em;
    
 }
 .nav ul li {
   width: 100%;
   text-align: center;
   margin-top: 1.9em;
   color: black;
 }
 .nav ul li:nth-child(1){
   margin-top: 40px;
  
 }
 .nav ul li:nth-child(2){
   margin-top: 70px;
   margin-bottom: 40px;
 }
 .nav ul li:nth-child(3), .nav ul li:nth-child(4), .nav ul li:nth-child(5), .nav ul li:nth-child(6) {
   text-align: left;
   margin: 20px 30px 20px 30px;
   list-style-type: none;
 }
 .nav ul li:nth-child(3){
   margin-top: 50px;
 }
 .nav ul li:nth-child(6) {
    margin-bottom: 50px;
 }

 .navLink {
    position: relative;
    width: 100%;
    height: 4em;
 }
 
 .navLink > div {
   display: inline-block;
   margin-left: 5px;
   padding-left: 15px;
   
   
 }
 .navLink > img {
   display: inline-block;
    width: 2em;
    height: 2em;
 }
 .navLink:hover{
    color: #FB8D02;
    font-size: 1.05em;
 }
 
 .navLink img::before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
 }

 .userProfile, .foodPic {

   margin-bottom: 10px;
 }

.userProfile img{
    min-width: 100px;
    border-radius: 15px;
 }

 .userProfile > div {
   margin-top: 30px;
   text-align: center;
  }
 .userProfile  > img, .foodPic > img {
   width: 5em;
   display:block;
   margin:auto;
   margin-top: 10px;
 }

 .navButtons{
    list-style: none;
    margin-top: 35px !important;
    margin-bottom: 10px !important;
  }
 
.navButtons > button{

   margin: 5px;
   cursor: pointer;
  
 }


 /* Mobile First */
 @media only screen and (min-width: 672px) and (max-width: 1087px){
    /* Sidebar */
    .trigger {
      width: 4em;
      width: 100%;
    }
    .nav {
       width: 4em;
       left: -15;
    }
   
    .nav:hover,
    .nav:focus,
    .trigger:hover + .nav,
    .trigger:focus + .nav {
       width: 15em;
    }
 }



 @media only screen and (min-width:1088px)  {

    /* Sidebar */
    .trigger {
       display: none;
    }
    
    .nav {
       width: 15em;
       left: 0;

    }
    
    .nav ul {
       top: 1.3em;
    }
 }
 
