@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&family=Paytone+One&display=swap');

*, *::before, *::after {
   box-sizing: border-box;
   font-family: 'Lato', sans-serif;
}

html, body {
   height: 100%;
   min-height: 100vh;
   
}


em {
   font-style: normal;
}

a {    
   text-decoration: none;
   color: inherit;
} 

/* Layout All Pages Except Hoome*/
.layout {
   display: flex;
   width: 100%; 
   
}
.fadeBox{
   opacity: 1;
   animation-name: fadeInOpacity;
	animation-iteration-count: 1;
	animation-timing-function: ease-in;
	animation-duration: 2s;
}

@keyframes fadeInOpacity {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.headings {
   display: flex;
   /* position: fixed; */
   margin: 0.8em;
   margin-top: 80px;
   overflow: hidden;
   font-family: "Paytone One", serif;
   color: #FB8D02;
   font-size: 2em;
  
}

.layoutContent {
   display: flex;
   justify-content: center;
   align-content: center;
   flex-direction: column;
   background: #EAEBF1;
   border-radius: 10px;
   margin: 1.5em;
   margin-top: 1em;
   padding: 1em;
   box-shadow:0 0 8px #a0a19f;
   height: 100%;

 

}

